import React from 'react';
import BoardHeader from './BoardHeader';
import BoardBottom from './BoardBottom'
import BoardLine from './BoardLine';
import Divider from './Divider'
import './MainBoard.css';
import Sound from 'react-sound'

import axios from 'axios';

class MainBoard extends React.Component {

    constructor(props) {
        super(props);
        this.state = { 
            playStatus: Sound.status.STOPPED,
            header: ['SERVICIO', 'TURNO', 'CONSULTA'],
            encolados: [
            ], 
            message : "Bienvenidos",
        }
    }

    handleSongFinishedPlaying = () => {
        this.setState({playStatus: Sound.status.STOPPED})
    }

    componentDidMount(){
        this.timer = setInterval(()=> this.getItems(), 200);
        this.timer = setInterval(()=> this.checkSound(), 600);
        this.timer = setInterval(()=> this.getConfig(), 6000);

        //this.getItems();
    }

    componentWillUnmount() {
        this.timer = null; 
    }

    // Recupera los elementos
    getItems(){
        axios.get('http://'+window.location.hostname+`:8081/qsmartboard/records.jsp`)
            .then(res => {
                console.log(res);
                this.setState({
                    encolados: res.data
                })
            })
    }

    getConfig() {
        axios.get('http://'+window.location.hostname+`:8000/boardconfig/1/`)
            .then(res => {
                console.log("CONFIGURING")
                console.log(res.data);
                this.setState({
                    message: res.data.welcome_message
                })
            })    
    }

    checkSound() {

        axios.get('http://'+window.location.hostname+`:8081/qsmartboard/calling.jsp`)
        .then(res => {
            console.log('calling invited');
            console.log(res.data.invited);
            if (res.data.invited === "true") {
            this.setState( { 
                playStatus: Sound.status.PLAYING
            })
            }
        })
    }

    

    render() {
        const rowHeight = 85 / this.props.rowNumber;
        const mainBoardStyle = {
            backgroundColor: this.props.bgColor,
        }

        const textStyle = {
            color: 'cyan',
            fontSize: '7rem'
        }

        const firstRowStyle = {
            color: 'whitesmoke',
            fontSize: '6rem'
        }

        const headerStyle = {
            color: 'lightyellow',
            fontSize: '4rem'
        }




        return (
            <div className="fullHeight" style={mainBoardStyle}>
                <Sound url="/ding.wav" playStatus={this.state.playStatus}
                   onFinishedPlaying={this.handleSongFinishedPlaying}/>
                <BoardHeader rowHeight={10} bgColor={this.props.bgColor} textStyle={headerStyle} message={this.state.message}/>
                <Divider style={{color:'white'}}/>
                <BoardLine header={this.state.header} rowHeight={rowHeight} bgColor={this.props.bgColor} textStyle={firstRowStyle}/>
                <Divider></Divider>
                {
                    this.state.encolados.map(element => 
                            <BoardLine key={element.addressRS} element={element} rowHeight={rowHeight} bgColor={this.props.bgColor} textStyle={textStyle}/>      
                    )
                }
              
                
            </div>
        )
    }
}


export default MainBoard;