import React from 'react';
import './BoardLine.css';
import {STATE_INVITED} from '../constants/constants'

class BoardLine extends React.Component {


    render() {

        const {textStyle, rowHeight, bgColor, element, header} = this.props;

        if (!element && !header)
            return null;
            
        let columns=header;
        if (!header) {
            columns= [( element.point.includes("#")? element.point.substr(0,element.point.indexOf("#")):"-"),
                        element.customerPrefix+ element.customerNumber, 
                        ( element.point.includes("#")? element.point.substr(element.point.indexOf("#")+1,100):element.point)];
        }

        const rowStyle={
            height: rowHeight+'%',
            border: 0,
            backgroundColor: bgColor,
            paddingTop: '3rem',
        }


        return (
            <div className="ui segment" style={rowStyle}>
                <div className="ui three column grid" style={textStyle}>
                    {columns.map ((col, index) => 
                        <div key={index} className={'column board-text ' + (element && element.state === STATE_INVITED? 'blink_me' : '')}>{col}</div>
                    )}
                </div>
            </div>
        )
    }
}

export default BoardLine;