import React from 'react';
import './BoardHeader.css';
import Marquee from './Marquee';

class BoardHeader extends React.Component {
    render() {
        const {rowHeight, bgColor, textStyle } = this.props;

        const rowStyle = {
            backgroundColor: bgColor,
            height: rowHeight+'%',
            paddingTop: '1em',
        }


        return(
            <>
                <Marquee rowStyle={rowStyle} textStyle={textStyle} message={'Bienvenidos al IVO'} />
            </>
            
        )
    }
}




export default BoardHeader;